import React, { Component } from 'react';
import 'animate.css';
import AOS from 'aos';
import 'aos/dist/aos.css'; // You can also use <link> for styles
// ..
AOS.init();

class About extends Component {
  render() {

    if(this.props.data){
      // var name = this.props.data.name;
      var profilepic= "images/"+this.props.data.image;
      var bio = this.props.data.bio;
      // var street = this.props.data.address.street;
      var city = this.props.data.address.city;
      var state = this.props.data.address.state;
      // var zip = this.props.data.address.zip;
      var phone= this.props.data.phone;
      var email = this.props.data.email;
      var resumeDownload = this.props.data.resumedownload;
      
    }

    return (
      <section id="about">
      <div className="row">
         <div className="three columns">
            <img data-aos="fade-right" className="profile-pic"  src={profilepic} alt="Rahul Profile Pic" />
         </div>
         <div data-aos="fade-left" className="nine columns main-col">
            <h2>About Me</h2>

            <p>{bio}</p>
            <div className="row">
               <div className="columns contact-details">
                  <h2>Contact Details</h2>
                  <p className="address">
                    <span>Rahul kumar</span><br />
                    <span>
                      {city}, {state}
                    </span><br />
                    <span>{phone}</span><br />
                    <span>{email}</span>
					        </p>
                  
               </div>
               
              <div className="columns download">
                  <p>
                     <a href={resumeDownload} target="_blank" rel="noopener noreferrer" className="button"><i className="fa fa-download"></i>Download Resume</a>
                  </p>
              </div>
            </div>
         </div>
      </div>

   </section>
    );
  }
}

export default About;
