import React, { Component } from 'react';
import 'tachyons';

import AOS from 'aos';
import 'aos/dist/aos.css'; // You can also use <link> for styles
// ..
AOS.init();

class Testimonials extends Component {
  render() {
    // var imagePath = 'images/testimonials/';
    if(this.props.data){
      var testimonials = this.props.data.testimonials.map(function(testimonials){
        // var testimonialsImage = imagePath+testimonials.image;
        return  <li key={testimonials.user}>
            <blockquote data-aos="fade-left" data-aos-duration="3000">
               <a href='https://www.linkedin.com/in/rahul-kumar943/'><p>{testimonials.text}</p></a>
               {/*<div className='fl w-15 pa2'>
                 <img className='br-100 h4 w4 dib' src={testimonialsImage} alt=''/>
               </div>*/}
               <div className='fl mt3 w-80 pa2'>
               <div>
                 <a href={testimonials.profile}><cite>{testimonials.user}</cite></a>
                </div>
               <div>
                 <cite>{testimonials.location}</cite>
                 </div>
                </div>
            </blockquote>
         </li>
      })
    }

    return (
      <section id="testimonials">
      <div className="text-container">
         <div className="row">

            <div className="two columns header-col">
               <h1><span>Testimonials</span></h1>
            </div>

            <div className="ten columns flex-container">
                  <ul className="slides">
                      {testimonials}
                  </ul>
               </div>
            </div>
         </div>
   </section>
    );
  }
}

export default Testimonials;
